body {
  margin: 0 auto;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #666666;
  font-weight: 400;
  background-color: #ffffff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  display: none; }

a {
  color: #666666;
  text-decoration: none; }

.header {
